div.hamInformationsBackground {
  position: relative;
  padding: 10px 0;
  width: 100%;
  min-height: auto;
  background-position: inherit;
  background-size: auto;
  background-repeat: no-repeat;
  background-image: url(./images/skos3.png);
}

img.hamArrow {
  width: 3%;
  margin: 20px auto;
  display: block;
  transform: rotate(180deg);
  content: url("./images/arrow-mobile.png");
}

.hamNutritionalValues {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  color: #fffad4;
  justify-content: space-around;
}

p.hamTitle {
  font-size: 20px;
  margin-bottom: 5px;
  color: #fffad4;
}

div.hamLineInformations {
  width: 180px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/line.png);
  margin-bottom: 15px;
}
div.hamLineAlergens {
  width: 80px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/line.png);
}

div.hamNutritionalValue {
  display: flex;
}

div.hamNutritionalValue p {
  flex-basis: 20%;
  margin: 0;
}

.hamAlergensDescription {
  color: #fffad4;
  display: none;
  transition: 0.3s;
}

@media (min-width: 768px) {
  div.hamLineInformations {
    width: 310px;
  }
  div.hamLineAlergens {
    width: 140px;
  }
  p.hamTitle {
    font-size: 35px;
  }
  div.hamNutritionalValue,
  .hamAlergensDescription {
    font-size: 18px;
  }
}

@media (min-width: 1024px) {
  img.hamArrow {
    margin: 20px auto;
    content: url("./images/arrow.png");
  }
  div.hamInformationsBackground {
    display: block;
    height: 0;
    background-image: none;
  }

  p.hamTitle {
    font-size: 23px;
    color: #eb5455;
    font-weight: 700;
  }
  div.hamLineInformations {
    width: 240px;
    height: 12px;
    background-image: url(./images/lineHam.png);
    background-size: contain;
    background-position: center;
  }

  div.hamLineAlergens {
    width: 120px;
    height: 11px;
    background-image: url(./images/lineHam.png);
    background-position: center;
  }
  .hamNutritionalValues {
    color: #eb5455;
    transition: 0.1s;
  }

  div.hamNutritionalValue,
  .hamAlergensDescription {
    font-size: 12px;
  }

  .hamAlergensDescription {
    color: #eb5455;
    display: block;
  }
}
