.hamInstructionTitle {
  color: #eb5455;
  font-size: 20px;
  margin-bottom: 5px;
}

div.hamLine {
  text-align: center;
  background-image: url(./images/line.png);
  width: 60%;
  height: 12px;
  background-repeat: no-repeat;
  background-size: contain;
}
div.hamSteps div.step1,
div.hamSteps div.step2,
div.hamSteps div.step3 {
  display: flex;
  justify-content: flex-start;
  color: #eb5455;
}

@media (min-width: 600px) {
  .hamInstructionTitle {
    font-size: 35px;
  }
  div.hamLine {
    height: 20px;
  }
}
